<template>
    <div class="mainc">
        <div class="choice-content">
            <span class="choice-item" :class="[index==1?'active':'']" data-index="1" v-if="nozhibo"
                  @click="choiceItem"></span>
            <span class="choice-item" :class="[index==2?'active':'']" data-index="2"
                  @click="choiceItem"></span>
            <span class="choice-item" :class="[index==3?'active':'']" data-index="3"
                  @click="choiceItem"></span>
        </div>
        <!---->
        <div class="details-item detail-color" v-if="nozhibo&&index==1">
            <div class="goods-zb" v-if="goodsMsg">
                <img class="goodsimg" :src="config+goodsMsg.banner">
                <div class="goods-text">
                    <div class="goods-name">{{goodsMsg.goods_name}}</div>
                    <div class="claim-time">{{takeTime}}</div>
                    <div class="goods-price">
                        <span class="color-icon"></span>{{goodsMsg.shop_price}}
                        <span class="s-text">{{goodsMsg.predict_price}}</span>
                    </div>
                </div>
                <button class="buy-goods" @click="viewTap" data-type="1" :data-url="nums"></button>
            </div>
            <div class="buy-msg" v-if="goodsMsg">
                <span class="buy-color">{{goodsMsg.client_num}}</span>{{goodsMsg.sale}}
            </div>
            <div class="zb-item" v-for="item in zbList" v-if="zbList">
                <div class="zb-time">{{item.create_time}}</div>
                <div class="zb-text">{{item.content}}</div>
                <div class="zb-img-content">
                    <a target="_blank" v-for="i in item.images" :href="config+i">
                        <img class="zb-img-item" data-index="0" data-key="0" :src="config+i">
                    </a>
                </div>
                <span class="praise-btn" :class="[item.zan!='false'?'active':'']" :data-index="0"
                      @click="likeFn"></span>
                <div class="praise-content">
                    <div class="praise-people">
                        <span class="icon"></span>
                        <span class="praise-text" v-for="(i,x) in item.user">
                              {{x == 0 ? i.nickname : ',' + i.nickname}}
                        </span>
                    </div>
                </div>
            </div>
            <div class="look-details" data-index="2" @click="choiceItem">--</div>
        </div>

        <!---->
        <div class="details-item" v-if="index==2">
            <!---->
            <div class="lunbo">
                <div class="d-picture">
                    <div class="swiper-container">
                        <swiper class="swiper-wrapper" :options="swiperOption" ref="mySwiper" v-if="goodsMsg">
                            <swiper-slide class="swiper-slide" v-for="item in goodsMsg.images">
                                <img class="swiper-img" :src="config+item.url" alt="">
                            </swiper-slide>
                            <div class="swiper-pagination" slot="pagination"></div>
                        </swiper>

                    </div>
                    <div class="gohome-page" @click="backHome"></div>
                    <div class="gopers-page" @click="viewTap" data-url="personal"></div>
                </div>
            </div>
            <!---->
            <div class="goodsMsg-tip1" v-if="goodsMsg">
                <div class="left-msg">
                    <span class="rmb-icon"></span>
                    <span class="s-price">{{goodsMsg.shop_price}}</span>
                    <span class="s-text">{{goodsMsg.predict_price}}</span>
                </div>
                <div class="right-msg">
                    <span class="r-msg-text">{{goodsMsg.die ? "" : goodsMsg.sale_time - timenum > serverTime ? "" : ""}}</span>
                    <span class="r-msg-time">{{goodsMsg.die ? "00:00:00" : intervalTime}}</span>
                </div>
            </div>
            <div class="goodsMsg-tip2" v-if="goodsMsg">
                <div class="goodsTip-name">{{goodsMsg.goods_name}}</div>
                <div class="follow-msg">{{goodsMsg.goods_explain}}
                    <span class="text">{{goodsMsg.click}}</span>
                </div>
            </div>

            <div class="tip-time tip-top" v-if="goodsMsg">{{saleTime}}
                <div class="right-tip">
                    <span class="text">{{goodsMsg.sale}}</span>{{goodsMsg.stock == 999999999 ? "" : '/' + goodsMsg.stock + ''}}
                </div>
            </div>
            <div class="tip-time tip-bottom">{{takeTime}}</div>

            <div class="choice-num" v-if="goodsMsg">
                <span class="left-num"></span>
                <div class="num-content">
                    <button class="minus" :class="[nums==1?'active':'']" :disabled="nums<=1" @click="changeNum"
                            data-num='-1'>-
                    </button>
                    <span class="nums">{{nums}}</span>
                    <button class="add" :class="[nums>=goodsMsg.max?'active':'']" :disabled="nums>=goodsMsg.max"
                            @click="changeNum" data-num="1">+
                    </button>
                </div>
            </div>

            <!---->
            <div class="goods-details1" v-if="goodsMsg">
                <div class="goods-title">
                    <span class="title-icon"></span>
                </div>
                <div class="table-content">
                    <div class="table-tr">
                        <div class="table-td td-1"></div>
                        <div class="table-td td-2">{{goodsMsg.company}}</div>
                    </div>
                    <div class="table-tr">
                        <div class="table-td td-1"></div>
                        <div class="table-td td-2">{{goodsMsg.brand}}</div>
                    </div>
                    <div class="table-tr">
                        <div class="table-td td-1"></div>
                        <div class="table-td td-2">{{goodsMsg.standard}}</div>
                    </div>
                    <div class="table-tr">
                        <div class="table-td td-1"></div>
                        <div class="table-td td-2">{{goodsMsg.place}}</div>
                    </div>
                </div>
            </div>
            <!--2-->
            <div class="goods-details2" v-if="goodsMsg">
                <div class="goods-title">
                    <span class="title-icon"></span>
                </div>
                <div class="details-text">
                    <div v-html="goodsMsg.desc"></div>
                </div>
            </div>

            <div class="order-step">
                <div class="step-title"></div>
                <div class="step-content">
                    <div class="step-text"></div>
                    <div class="text"></div>
                    <div class="step-text"></div>
                    <div class="text"></div>
                    <div class="step-text"></div>
                    <div class="text"></div>
                </div>
            </div>

            <div class="bottom-tip">
                <img src="https://thl.lecuntao.com/youxuan/icons/fdd-img.png" class="fdd-img">
                <div class="tip-text"> </div>
            </div>
        </div>

        <!---->
        <div class="details-item" v-if="index==3">
            <div class="cart-empty" v-if="buyPeople.length==1">
                <div class="empty-img">
                    <img src="https://thl.lecuntao.com/youxuan/icons/shopping.png" class="shoppingImg">
                </div>
                <div class="empty-text"></div>
                <div class="empty-look" @click="choiceItem" data-index="2">--</div>
            </div>

            <div class="buy-people-content" v-if="goodsMsg">
                <div class="buy-tips">
                    <span class="text">{{goodsMsg.client_num}}</span>{{goodsMsg.sale}}
                </div>
                <div class="people-item" v-for="item in buyPeople" v-if="buyPeople">
                    <div class="flex-item">
                        <img :src="item.face||(config+'/youxuan/icons/logo.png')"
                             alt="" class="user-head">
                        <span class="user-name">{{item.nickname}}</span>
                    </div>
                    <div class="flex-item middle">
                        <span class="text">{{item.buy_num}}</span>
                    </div>
                    <div class="flex-item right">{{item.pay_time}}</div>
                </div>
            </div>
        </div>

        <div class="sellOut-tip" v-if="goodsMsg" :class="[isSellOut||goodsMsg.die?'disabled':'']">
            {{goodsMsg.die ? "" : ""}}
        </div>

        <!---->
        <div class="btm-msg" v-if="goodsMsg">
            <div class="cart-msg icon-cart" @click="viewTap" data-url="cart">
                <span class="num-tip" v-if="cartNum>0">{{cartNum}}</span>
            </div>
            <button class="joinCart-btn" :class="[isSellOut||goodsMsg.die?'disabled':'']" @click="viewTap"
                    data-type="2">
            </button>
            <button class="buy-btn" :class="[isSellOut||goodsMsg.die?'disabled':'']" @click="viewTap" data-type="1"
                    :data-url="nums">
            </button>
        </div>
    </div>
</template>

<script>
    import {passTime} from "../../assets/passTime"
    import "../../assets/format"
    import SwiperSlide from "../../../node_modules/vue-awesome-swiper/src/slide";

    export default {
        components: {SwiperSlide},
        name: "detail",
        data(){
            return {
                swiperOption: {
                    pagination: {
                        el: '.swiper-pagination',
                    },
                    autoplay: true,
                },
                config: this.GLOBAL.config,
                index: 2,
                gid: '', //id 
                shopid: '', //id 
                keys: '', //  
                fddImg: '', //
                shoppingImg: '', //
                isSellOut: false, //
                cartNum: 0, //
                serverTime: Math.floor(new Date().getTime() / 1000),
                serverTime_h: '', //
                timenum: 0, //
                intervalTime: '--:--:--', //
                saleTime: '', //
                takeTime: '', //
                nums: 1, //or
                nozhibo: false,
                zbList: [], //
                goodsMsg: null, //
                intTime: 0,
                buyPeople: []//
            }
        },
        created: function () {
            var gid = this.$route.query.gid;
            var shopid = this.$route.query.shopid;
            this.gid = gid;
            this.shopid = shopid;
            this.getGoods()
        },
        mounted() {

        },

        methods: {
            // oror
            viewTap: function (e) {
                var urls = e.target.dataset.url;
                var types = e.target.dataset.type;
                var role = this.GLOBAL.role;
                var userMsg = this.GLOBAL.userMsg;
                if (!role || !userMsg || (role == 1 && !userMsg.uid) || (role == 2 && !userMsg.store_id)) { //
                    this.toast = this.$createToast({
                        txt: '',
                        time: 600,
                        type: 'txt'
                    })
                    this.toast.show()
                    setTimeout(() => {
                        this.$router.push({name: 'login'});
                    }, 600)
                    return
                }
                if (types) {
                    var goodsMsg = this.goodsMsg;
                    if (goodsMsg.die) {
                        var msg = goodsMsg.sale_time - this.timenum > this.serverTime ? '' : '';
                        this.toast = this.$createToast({
                            txt: msg,
                            time: 1000,
                            type: 'txt'
                        })
                        this.toast.show()
                        return
                    }
                    if (goodsMsg.start_sale_time > this.serverTime_h) {
                        this.toast = this.$createToast({
                            txt: goodsMsg.start_sale_time + ' ',
                            time: 1000,
                            type: 'txt'
                        })
                        this.toast.show()
                        return
                    }
                    if (this.isSellOut) {
                        this.toast = this.$createToast({
                            txt: '',
                            time: 1000,
                            type: 'txt'
                        })
                        this.toast.show()
                        return
                    }
                    if (types == 1) { //
                        if (this.nums > goodsMsg.people_max) {
                            this.toast = this.$createToast({
                                txt: ' ' + goodsMsg.people_max + '',
                                time: 1000,
                                type: 'txt'
                            })
                            this.toast.show();
                            return
                        }
                        var saveData = [{
                            copy_id: goodsMsg.copy_id,
                            id: goodsMsg.id,
                            img: goodsMsg.banner,
                            goods_name: goodsMsg.goods_name,
                            standard: goodsMsg.standard,
                            take_time: new Date(goodsMsg.take_time * 1000).format('MMdd'),
                            shop_price: goodsMsg.shop_price,
                            predict_price: goodsMsg.predict_price,
                            max: goodsMsg.max
                        }];
                        localStorage.setItem('order', JSON.stringify(saveData));
                        this.$router.push({path: 'confirm', query: {type: 1, num: urls}});
                    } else if (types == 2) { //
                        if (goodsMsg.cart_num >= goodsMsg.stock) {
                            this.toast = this.$createToast({
                                txt: '' + goodsMsg.stock + '',
                                time: 1000,
                                type: 'txt'
                            })
                            this.toast.show();
                            return
                        }
                        if (this.cartNum + this.nums > goodsMsg.people_max) {
                            this.toast = this.$createToast({
                                txt: ' ' + goodsMsg.people_max + '',
                                time: 1000,
                                type: 'txt'
                            })
                            this.toast.show();
                            return
                        }
                        var num = this.nums - 0;
                        var updata = {
                            copy_id: goodsMsg.copy_id,
                            num: num
                        }
                        const pop = this.$createToast({
                            time: 10000,
                        });
                        pop.show()

                        this.axios.get('/minapi/cart/addcart', {
                            params: {
                                role: role,
                                cart: updata,
                                access_token: userMsg.access_token
                            }
                        }).then(res => {
                            var datas = res.data;
                            pop.hide();
                            if (datas.error_code == 0) {
                                var cartNum = this.cartNum - 0 + num;

                                var recart = {
                                    cartNum: cartNum,
                                    keys: !this.keys ? "-1" : this.keys
                                };
                                localStorage.setItem('refresh', 2); //

                                localStorage.setItem('cartNum', JSON.stringify(recart));


                                this.cartNum = cartNum;
                                this.mesg = this.$createToast({
                                    txt: '',
                                    time: 1000,
                                    type: 'txt'
                                })
                                this.mesg.show();
                            } else {
                                this.info = this.$createToast({
                                    txt: '',
                                    type: 'txt'
                                })
                                this.info.show();
                            }
                        }).catch(error => {
                            this.toast = this.$createToast({
                                txt: ' addcart',
                                type: 'txt'
                            })
                            this.toast.show();
                        })
                    }
                } else { //
                    if (urls.indexOf('personal') != -1 && role == 2) {
                        this.$router.push({name: 'personal2'});
                    } else {
                        this.$router.push({name: urls});
                    }
                }
            },
            //
            backHome: function () {
                this.$router.push({name: 'index'});
            },
            //
            choiceItem: function (e) {
                var ind = e.target.dataset.index;
                if (ind != this.index) {
                    this.index = ind;
                    if (ind == 3 && this.buyPeople.length == 0) {
                        this.getBuyPeople()
                    }
                }
            },
            //
            changeNum: function (e) {
                var num = Number(e.target.dataset.num);
                var nums = this.nums + num;
                if (nums == this.goodsMsg.max) {
                    this.toast = this.$createToast({
                        txt: ' ' + nums + '',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show()
                }
                this.nums = nums;
            },
            //
            getGoods: function () {
                var role = this.GLOBAL.role;
                var userMsg = this.GLOBAL.userMsg;
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/goods/goodsDetail', {
                    params: {
                        role: role || '',
                        access_token: userMsg? userMsg.access_token : '',
                        id: this.gid,
                        store: this.shopid
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        var obj = datas.data;
                        var tv = [];
                        if (obj.tv.length > 0) {
                            this.nozhibo = true;
                            this.index = 1;
                            tv = obj.tv;
                            var len = tv.length;
                            for (var i = 0; i < len; i++) {
                                tv[i].create_time = passTime(tv[i].create_time * 1000);
                                if (tv[i].user.length > 0 && !tv[i].user[0].face) {
                                    tv[i].user.shift();
                                }
                                if (tv[i].store && tv[i].store.length > 0) {
                                    tv[i].user = tv[i].user.concat(tv[i].store)
                                }
                            }
                        }
                        obj.desc = obj.desc.replace(/<img/ig, '<img class="html-img"');
                        obj.desc = obj.desc.replace(/src="/ig, 'src="' + this.config);
                        obj.desc = obj.desc.replace(/<p/ig, '<p class="html-p"');
                        obj.max = obj.stock == 999999999 ? 999999999 : obj.stock - obj.sale;

                        this.serverTime = obj.server_time,
                            this.saleTime = new Date(obj.putaway_time * 1000).format('MMdd hh:mm'), //
                            this.takeTime = new Date(obj.take_time * 1000).format('MMdd hh:mm'), //
                            this.goodsMsg = obj,
                            this.zbList = tv,
                            this.cartNum = obj.cart_num || 0,
                            this.isSellOut = obj.max <= 0
                        this.setTime(); //goodsMsg 
                    } else {
                        this.info = this.$createToast({
                            txt: '',
                            type: 'txt'
                        })
                        this.info.show();
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' ',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            //
            getBuyPeople: function () {
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show();
                this.axios.get('/minapi/goods/goodsclient', {
                    params: {
                        id: this.gid,
                        store: this.shopid
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        var datalist = datas.data;
                        for (var i = 0, len = datalist.length; i < len; i++) {
                            datalist[i].pay_time = new Date(datalist[i].pay_time * 1000).format('yyyy-MM-dd hh:mm:ss')
                        }
                        this.buyPeople = datalist;
                    } else {
                        this.info = this.$createToast({
                            txt: 'ID',
                            type: 'txt',
                            time: 1000
                        })
                        this.info.show();
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' ',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            //
            setTime: function () {
                this.timeFn()
                if (this.goodsMsg.die) { //
                    this.goodsMsg.die = true;
                    this.toast = this.$createToast({
                        txt: '',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                var intTime = setInterval(() => {
                    this.timeFn()
                }, 1000)
                this.intTime = intTime
            },
            timeFn: function () {
                var timenum = this.timenum;
                var times = this.goodsMsg.sale_time - this.serverTime - timenum; // -  > 0 
                var serverTime_h = new Date((this.serverTime - 0 + timenum) * 1000).format('h'); //    start_sale_time 
                if (times <= 0) { // 
                    times = this.goodsMsg.end_sale_time - this.serverTime - timenum;
                }
                if (times <= 0) { //
                    clearInterval(this.intTime);
                    this.goodsMsg.die = true;
                    this.intervalTime = '00:00:00'
                    return
                }
                var h = parseInt(times / 3600);
                var m = parseInt(times % 3600 / 60);
                var s = parseInt(times % 60);
                this.serverTime_h = serverTime_h;
                this.timenum = timenum + 1;
                this.intervalTime = (h < 10 ? '0' + h : h) + ':' + (m < 10 ? '0' + m : m) + ':' + (s < 10 ? '0' + s : s);
            },
            //
            likeFn: function (e) {
                var keys = e.target.dataset.index;
                var zbMsg = this.zbList[keys];
                var userMsg = this.GLOBAL.userMsg;
                var userInfo = this.GLOBAL.userInfo;
                if (!userMsg) { //
                    this.toast = this.$createToast({
                        txt: '',
                        time: 600,
                        type: 'txt'
                    })
                    this.toast.show()
                    setTimeout(() => {
                        this.$router.push({name: 'login'});
                    }, 600)
                    return
                }
                if (zbMsg.zan == 'true') {
                    return
                }

                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/goods/clicktv', {
                    params: {
                        role: 1,
                        access_token: 'd2b9746aa13cfc81e2c75cec378ba405',
                        id: zbMsg.info_id
                    }
                }).then(res => {
                    var datas = res.data;
                    var error_msg = datas.error_msg;
                    pop.hide();
                    if (datas.error_code == 0) {
                        var zbList = this.zbList;
                        zbList[keys].zan = 'true';
                        zbList[keys].user.push({
                            nickname: userInfo.nickName
//                            nickname:''
                        })
                        this.zbList = zbList;
                        const toast = this.$createToast({
                            txt: '',
                            type: 'correct'
                        })
                        toast.show()
                    } else {
                        const toast = this.$createToast({
                            txt: error_msg,
                            type: 'correct'
                        })
                        toast.show()
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: 'clicktv',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            }
        },

        computed: {}
    }
</script>

<style scoped>
    @import url(./static/css/d_details.css);
</style>